* {
  margin: 0;
  padding: 0;
  border: 0;
}
body {
  height: 100vh;
  background-color: #ECE3F0;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: -apple-system, Helvetica, sans-serif;
}
.counter {
  width: 420px;
  border-radius: 16px;
  background-color: #F2F1F0;
  box-shadow: 0 0 3rem rgba(24, 32, 40, 0.25);
  text-align: center;
}
.counter header {
  padding: 1.75rem 0 1.5rem;
  border-bottom: 1px solid #d4dce8;
}
.counter h1 {
  color: #4E514E;
  font-size: 1.75rem;
  letter-spacing: -0.01em;
}
.counter p {
  margin-top: 0.5rem;
  color: #4E514E;
  font-size: 1rem;
  letter-spacing: -0.01em;
}
.counter strong {
  color: #4B443C;
  font-size: 1em;
}
.counter .stepper {
  display: flex;
  justify-content: center;
  padding: 3rem 0 3.25rem;
}
.counter .number {
  width: 6rem;
  color: #4D0011;
  font-size: 2.5rem;
  font-weight: 700;
  line-height: 2.925rem;
}
.counter button {
  position: relative;
  text-indent: -9999rem;
  width: 3rem;
  height: 3rem;
  border-radius: 3rem;
  outline: 0;
  border: 1px solid #dce4f0;
  background-color: #EDE4E3;
  cursor: pointer;
  transition: all 0.24s ease-in-out;
}
.counter button::before,
.counter button::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 1.375rem;
  height: 3px;
  background-color: #8696b0;
  transition: all 0.24s ease-in-out;
}
.counter button:first-child::after {
  display: none;
}
.counter button:last-child::after {
  transform: rotate(-90deg) translate(-50%, -50%);
  transform-origin: 0 0;
}
.counter button:not(:disabled):hover::before,
.counter button:not(:disabled):hover::after {
  background-color: #545c70;
}
.counter button:not(:disabled):hover {
  border-color: #b4c0d4;
}
.counter button:disabled {
  opacity: 0.5;
}
